import React, { useEffect, useState } from "react";
import { localRoute } from '../../../routes/localRoutes';
import AOS from "aos";
import "aos/dist/aos.css";
import { useHistory } from 'react-router-dom';

const FaqItem = ({ id, question, answer, isCollapsed, toggleCollapse }) => {
    return (
        <div className="mt-50">
            <div style={{ display: 'flex', alignItems: 'center', justifyContent: 'space-between' }}>
                <h3 className="font-montserrat fs-20 fw-700" onClick={() => toggleCollapse(id)} style={{ color: '#BABABA', cursor: 'pointer' }}>
                    {question}
                </h3>
                <h3 className="font-montserrat" onClick={() => toggleCollapse(id)} style={{ color: '#BABABA', cursor: 'pointer', marginRight: '10px' }}>
                    {isCollapsed ? <>&#9660;</> : <>&#9650;</>}
                </h3>
            </div>
            <hr style={{ borderColor: '#BABABA' }} />
            {!isCollapsed && <p className="font-montserrat fs-16 fw-400" style={{ color: '#fff' }}>
                {answer}
            </p>}
        </div>
    );
};

export default function AskQuestion() {
    const history = useHistory()

    useEffect(() => {
        window.scrollTo(0, 0)
        AOS.init({
            duration: 2000
        });
    }, [])

    const [faqItems, setFaqItems] = useState([
        {
            id: 0,
            question: "HOW LONG DO WE GET TO KEEP THE CARS FOR THE WEDDING OR BARAAT PACKAGE?",
            answer: "Our wedding or baraat package offers flexible rental durations, ranging from a few hours to a full day or more, tailored to your needs. Contact us for specific details.",
            isCollapsed: true
        },
        {
            id: 1,
            question: "WHAT IS THE CANCELLATION POLICY?",
            answer: "Our cancellation policy allows you to cancel your booking up to 48 hours before the rental period begins for a full refund. Cancellations made within 48 hours are subject to a cancellation fee.",
            isCollapsed: true
        },
        {
            id: 2,
            question: "HOW LONG DO WE GET TO KEEP THE CARS FOR THE WEDDING OR BARAAT PACKAGE?",
            answer: "Our wedding or baraat package offers flexible rental durations, ranging from a few hours to a full day or more, tailored to your needs. Contact us for specific details.",
            isCollapsed: true
        },
        {
            id: 3,
            question: "WHAT IS THE CANCELLATION POLICY?",
            answer: "Our cancellation policy allows you to cancel your booking up to 48 hours before the rental period begins for a full refund. Cancellations made within 48 hours are subject to a cancellation fee.",
            isCollapsed: true
        }
    ]);

    const toggleCollapse = (id) => {
        setFaqItems(prevState =>
            prevState.map(item =>
                item.id === id ? { ...item, isCollapsed: !item.isCollapsed } : item
            )
        );
    };

    return (
        <div>

            <div className="mt-50" style={{ background: 'rgb(17, 17, 17)' }}>
                <div className="container feature-blog-one blog-page-bg">
                    <div style={{ textAlign: 'center' }}>
                        <h2 className="font-montserrat" style={{ color: '#BABABA' }}>
                            Frequently Asked Questions Renting a Ferrari F8
                        </h2>
                        <p className="font-montserrat" style={{ color: '#BABABA' }}>
                            These are the questions we've received from our real-time customers about renting our exotic Ferrari F8 from Monaco Experiences.
                        </p>
                    </div>

                    {faqItems.map(item => (
                        <FaqItem
                            key={item.id}
                            id={item.id}
                            question={item.question}
                            answer={item.answer}
                            isCollapsed={item.isCollapsed}
                            toggleCollapse={toggleCollapse}
                        />
                    ))}
                </div>
            </div>
        </div >
    )
}
