import React, { useState } from 'react'
import { useHistory } from 'react-router-dom'
import { localRoute } from "../../routes/localRoutes";
import { useEffect } from 'react';
import Button from '@material-ui/core/Button';

export default function Header(props) {
    const navRef = React.useRef(null);

    const history = useHistory()

    const handleBackCars = () => {
        navRef.current.classList.toggle("show");
        history.push(localRoute.cars)
        window.scrollTo(0, 0)
    };

    const handleBackMansions = () => {
        navRef.current.classList.toggle("show");
        history.push(localRoute.mansions)
        window.scrollTo(0, 0)
    };

    const handleBackHome = () => {
        navRef.current.classList.toggle("show");
        history.push(localRoute.home)
        window.scrollTo(0, 0)
    };
    const handleBackGallery = () => {
        navRef.current.classList.toggle("show");
        history.push(localRoute.gallery)
        window.scrollTo(0, 0)
    };

    const handleOpenEnquiry = () => {
        navRef.current.classList.toggle("show");
        window.location.replace("/#enquiry-form");
    }


    const handleBackAbout = () => {
        navRef.current.classList.toggle("show");
        history.push(localRoute.about)
    };
    const currentRoute = useHistory().location.pathname.toLowerCase();


    console.log('currentRoute', currentRoute)


    const onToggleClick = (e) => {
        navRef.current.classList.toggle("show");
    };



    return (
        <div class="theme-main-menu sticky-menu" style={{ backgroundColor: '#000000' }}>
            <div style={{ display: "flex", justifyContent: "space-between", alignItems: "center" }} >
                <div class="logo">
                    <a style={{ cursor: "pointer" }} onClick={handleBackHome}>
                        <img class="taskmo-logo" src="../../../../monaco_assets/monaco-footer.png" alt="monaco-logo" />
                    </a>
                </div>

                <nav id="mega-menu-holder" class="header-margin navbar navbar-expand-lg" >
                    <div class="container nav-container">
                        <button class="navbar-toggler navbar-toggler-right" onClick={onToggleClick} type="button" data-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
                            <i class="flaticon-menu-of-three-lines"></i>
                        </button>

                        <div class="navbar-collapse collapse" ref={navRef} >

                            <div class="font-montserrat d-lg-flex justify-content-between align-items-center">
                                <ul class="fw-500 font-montserrat navbar-nav">
                            
                                    <li class=" nav-item position-static active">
                                        <a style={{ cursor: "pointer" }} onClick={handleBackMansions} class="nav-link">
                                            <p className={currentRoute == "/house-of-monaco" ? "tabactive" : ""}>house of monaco</p>
                                        </a>
                                    </li>

                                    <li class=" nav-item position-static active">
                                        <a style={{ cursor: "pointer" }} onClick={handleBackCars} class="nav-link">
                                            <p className={currentRoute == "/cars" ? "tabactive" : ""}>vehicles</p>
                                        </a>
                                    </li>

                                    <li class="nav-item position-static active">
                                        <a style={{ cursor: "pointer" }} onClick={handleBackGallery} class="nav-link">
                                            <span className={currentRoute == "/gallery" ? "tabactive" : ""}>gallery</span>
                                        </a>
                                    </li>
                                    <li class="nav-item position-static active">
                                        <a style={{ cursor: "pointer" }} onClick={handleOpenEnquiry} class="nav-link">
                                            <span className={currentRoute == "/#enquiry-form" ? "tabactive" : ""}>reviews</span>
                                        </a>
                                    </li>
                                    <li class="nav-item  position-static active">
                                        <a style={{ cursor: "pointer" }} onClick={handleBackAbout} class="nav-link">
                                            <span className={currentRoute == "/about-us" ? "tabactive" : ""}>about us</span>
                                        </a>
                                    </li>
                                </ul>
                            </div>
                        </div>
                    </div>
                </nav>
            </div>
        </div>
    )
}
